import React from 'react';

import Section from '@components/Section';
import Headings from '@components/Headings';

import styled from '@emotion/styled';
import mediaqueries from '@styles/media';
const StyledLink = styled(props => <a {...props} />)`
  cursor: pointer;
  color: #6166dc;
  padding: 10px;
  margin-left: 10px;
  margin-top: 10px;
  background-color: #f1f1f1;
  text-align: center;
`;

const Tutorials: React.FC<{}> = () => {
  return (
    <Section narrow>
      <Heading>Guide book</Heading>
      <SubscriptionContainer>
        <StyledLink href={'/guidebook-the-javascript-monorepo'}>
          {' Guidebook: The Javascript Monorepo'}
        </StyledLink>
        <StyledLink
          href={'https://aguidehub.com/tags/photoshop'}
          target="_BLANK"
        >
          {' Photoshop Guide'}
        </StyledLink>
      </SubscriptionContainer>

      <Heading>Tutorials</Heading>
      <SubscriptionContainer>
        <Anchro href="https://infinitbility.com/react-native">
          {'React Native'}
        </Anchro>
      </SubscriptionContainer>

      <Heading>Categories</Heading>
      <SubscriptionContainer>
        <StyledLink href={'/category/laravel'}>{' Laravel'}</StyledLink>
        <StyledLink href={'/category/guidebook'}>{' Guidebook'}</StyledLink>
        <StyledLink href={'/category/typescript'}>{' TypeScript'}</StyledLink>
        <StyledLink href={'/category/react-native'}>
          {' React Native'}
        </StyledLink>
        <StyledLink href={'/category/node'}>{' Node'}</StyledLink>
        <StyledLink href={'/category/apache'}>{' Apache'}</StyledLink>
        <StyledLink href={'/category/ubuntu'}>{' Ubuntu'}</StyledLink>
        <StyledLink href={'/category/css'}>{' CSS'}</StyledLink>
        <StyledLink href={'/category/security'}>{' Security'}</StyledLink>
        <StyledLink href={'/category/react-navigation'}>
          {' React Navigation'}
        </StyledLink>
        <StyledLink href={'/category/cron'}>{' CRON'}</StyledLink>
        <StyledLink href={'/category/yajra'}>{' Yajra'}</StyledLink>
        <StyledLink href={'/category/firebase'}>{' Firebase'}</StyledLink>
        <StyledLink href={'/category/python'}>{' Python'}</StyledLink>
        <StyledLink href={'/category/javascript'}>{' Javascript'}</StyledLink>
        <StyledLink href={'/category/php'}>{' PHP'}</StyledLink>
        <StyledLink href={'/category/cakephp'}>{' Cakephp'}</StyledLink>
        <StyledLink href={'/category/cloudflare'}>{' Cloudflare'}</StyledLink>
        <StyledLink href={'/category/react'}>{' React'}</StyledLink>
        <StyledLink href={'/category/svn'}>{' SVN'}</StyledLink>
        <StyledLink href={'/category/xcode'}>{' Xcode'}</StyledLink>
        <StyledLink href={'/category/aws-s3'}>{' AWS S3'}</StyledLink>
        <StyledLink href={'/category/mustache'}>{' Mustache'}</StyledLink>
        <StyledLink href={'/category/windows'}>{' Windows'}</StyledLink>
        <StyledLink href={'/category/microsoft-office'}>
          {' Microsoft Office'}
        </StyledLink>
        <StyledLink href={'/category/gatsby'}>{' Gatsby'}</StyledLink>
        <StyledLink href={'/category/ssl'}>{' SSL'}</StyledLink>
        <StyledLink href={'/category/infinitbility'}>
          {' Infinitbility'}
        </StyledLink>
      </SubscriptionContainer>
    </Section>
  );
};

export default Tutorials;

const SubscriptionContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-bottom: 10px;
  //   padding: 64px 0 55px;
  //   margin: 50px auto 100px;
  background: ${p => p.theme.colors.card};
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.05);
  z-index: 1;
  overflow-x: auto;
`;

const Heading = styled(Headings.h3)`
  margin-bottom: 20px;
  margin-top: 20px;
  color: ${p => p.theme.colors.grey};

  ${mediaqueries.tablet`
    margin-bottom: 15px;
  `}
`;

const Anchro = styled.a`
  cursor: pointer;
  color: #6166dc;
  padding: 10px;
  margin-left: 10px;
  margin-top: 10px;
  background-color: #f1f1f1;
  text-align: center;
`;
